header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header_container {
    position: relative;
    text-align: center;
    height: 100%;
}

/* =============================== CTA =============================== */

.cta {
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    margin-top: 2.5rem;
}

/* =============================== SOCIALS =============================== */

.header_socials {
    font-size: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials:after {
    content: "";
    background: var(--color-primary);
    width: 1px;
    height: 2rem;
}

/* =============================== ME =============================== */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* =============================== SCROLL_DOWN =============================== */

.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: .9rem;
}

/* ======================== MEDIA QUERIES (MEDUIM DEVICES) ========================= */
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
    .scroll_down, .header_socials {
        display: none;
    }
}